.company {
    margin-bottom: 127px;
}
.company__row {
    margin: 0 auto;
    display: flex;
    justify-content: center;
    
    
}
.company__row div:not(:last-child){
    margin-right: 47px;
    
}
@media (max-width: 710px) {
    .company__row{
        flex-direction: column;
        align-items: center;
    }
    .company__row div:not(:last-child) {
        margin-right:  0;
    }
    .company__row div {
        margin-bottom: 20px;
    }
    .company {
        margin-bottom: 40px;
    }
}