.footer {
    padding: 175px 178px 27px 178px;
    background: #031B34;
}
.footer__content {
    display: flex;
    justify-content: space-between;
}
.footer__title {
    text-align: center;
    font-family: Manrope;
    font-size: 62px;
    font-style: normal;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -2.48px;
    max-width: 821px;
    background: linear-gradient(#AE67FA, #F49867);
    background-clip: text;
    color: transparent;
    margin-bottom: 64px;
}
.footer__button {
    color: #FFF;
    font-family: Manrope;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    background: transparent;
    padding: 21px;
    border: 1px solid #FFF;
    margin: 0 auto;
    display: block;
    margin-bottom: 180px;
    transition: all ease .5s;
}
.footer__button:hover {
    opacity: 0.7;
}
.footer__content-column1 {
    margin-right: 237px;

}
.footer__logo {
    margin-bottom: 25px;
}
.footer__right {
    max-width: 170px;
    color: #FFF;
    font-family: Manrope;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}
.footer__list-title {
    color: #FFF;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-bottom: 36px;
}
.footer__list li a{
    color: #FFF;
    font-family: Manrope;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}
.footer__list li:not(:last-child) {
    margin-bottom: 26px;
}
.footer__links {
    display: flex;
    gap: 0 186px;
    margin-bottom: 83px;
}
.footer__list-wrapper {
    max-width: 120px;
}
.footer__copyright {
    color: #FFF;
    font-family: Manrope;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0 auto;
    text-align: center;
}
@media (max-width:1220px) {
    .footer__content {
        flex-direction: column;
    }
    .footer__content-column1 {
        text-align: center;
        margin: 0 auto 30px auto;
    }
    .footer__links {
        justify-content: center;
        text-align: center;
    }
}
@media (max-width:1000px) {
    .footer__title {
        font-size: 50px;
    }
}
@media (max-width:700px) {
    .footer__title {
        font-size: 34px;
    }
    .footer__links {
        flex-direction: column;
        text-align: center;
        margin: 0 auto 20px auto;
    }
    .footer__list-wrapper {
        margin-bottom: 20px;
    }
}
@media (max-width:630px) {
    .footer__title {
        font-size: 34px;
    }
    .footer__button {
        margin-bottom: 30px;
    }
    .footer {
        padding: 50px 30px 27px 30px;
    }
}
@media (max-width:500px) {
    .footer__title {
        font-size: 24px;
        margin-bottom: 20px;
    }
}

