.header {
    min-height: 58px;
    align-items: center;
    padding-top: 55px;
}
.nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    
}
.left_part {
    display: flex;
}
.logo {
    margin-right: 69px;
}
.nav__list {
    display: flex;
}
   
.nav__list a {
    color: #FFF;
    font-family: Manrope;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    margin-top: 2px;

}

.nav__links a:not(:last-child) {
    margin-right: 54px;
}
.registration {
    align-items: center;
    display: inline-flex;
    justify-content: end;
}
.registration__sign-in {
    color: #FFF;
    font-family: Manrope;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    background-color: transparent;
    margin-right: 26px;
}
.registration__sign-up {
    padding: 16px 44px;
    background-color: #FF4820;
    color: #FFF;
    font-family: Manrope;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    transition: all 0.3s ease;
    border-radius: 5px;
    margin-right: 15px;
}
.registration__sign-up:hover {
    opacity: 0.7;
}

.modal {
    position: fixed;
    z-index: 40;
    top: 50%;
    left: 50%;
    border-radius: 20px;
    max-width: 500px;
    height: 600px;
    background: radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
    transform: translate(-50%, -50%);
    transition: all 0.5s ease;
    visibility: hidden;
    opacity: 0;
}
.modal__show {
    display: block;
    visibility: visible;
    opacity: 1;
}
.modal__content {
    padding: 60px;
    position: relative;
}
.modal__form {
    justify-content: space-between;
    min-height: 400px;
    align-items: center;
}
.modal__content_text {
    color: #FF4820;
    text-align: center;
    margin-bottom: 26px;
    font-size: 40px;
    font-family: Manrope;
    font-weight: 800;
}
.modal__form input {
    width: 100%;
    padding: 15px;
    border-radius: 10px;
    margin-bottom: 30px;
    font-size: 18px;
   
}
.modal__form button {
    color: white;
    text-align: center;
    margin-bottom: 20px;
    font-size: 30px;
    font-family: Manrope;
    font-weight: 800;
    padding: 20px 90px;
    text-align: center;
    margin: 0 auto;
    display: block;
    background-color: #FF4820;
    border-radius: 20px;
    transition: all ease 0.5s;
}
.modal__form button:hover {
    opacity: 0.7;
}
.modal__close {
    position: absolute;
    width: 50px;
    top: 20px;
    right: 20px;
    cursor: pointer;

}

.burger-menu {
    width: 50px;
    height: 40px;
    display: inline-flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    z-index: 5;
}
.burger-menu span {
    width: 100%;
    height: 4px;
    background-color: white;
}


.phone__list, .burger-menu {
    display: none;
}
@media (max-width:1120px) {
    .nav__list {
        display: none;
    }
    .burger-menu {
        display: flex;
    }
   
   
    .phone-bg {
        opacity: 0;
        visibility: hidden;
        width: 100%;
        height: 100%;
        background: radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
        position: fixed;
        top: 0;
        left: -100%;
        display: flex;
        justify-content: center;
        align-items: center;
        transition-duration: 0.5s;
        
    }
    .phone-show {
        opacity: 1;
        visibility: visible;
        left: 0;
    }
    .phone__list {
        display: block;
        position: relative;
        z-index: 4;
        text-align: center;
       
    }
       
    .nav__list-item {
        color: #FFF;
        font-family: Manrope;
        font-size: 40px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        margin-bottom: 50px;
    }
    
}
@media (max-width:520px) {
    .logo {
        margin-right: 20px;
        max-width: 70px;
    }
    .registration__sign-up {
        padding: 10px 20px;
    }
}