.what {
    padding: 57px 55px 63px 55px;
    background: #082c54;
    margin-bottom: 201px;

}
.what__top {
    display: flex;

}

.what__top_title {
    margin-right: 154px;
    
}
.what__top_text {
    color: #81AFDD;
    font-family: Manrope;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 30px;
    max-width: 731px;
    margin-bottom: 91px;
}
.what__middle {
    display: flex;
    justify-content: space-between;
    margin-bottom: 92px;
}
.what__middle_text {
    font-family: Manrope;
    font-size: 34px;
    font-style: normal;
    font-weight: 800;
    line-height: 45px;
    max-width: 472px;
    background: linear-gradient(#AE67FA, #F49867);
    background-clip: text;
    color: transparent;
}
.what__middle a {
    color: #FF8A71;
    font-family: Manrope;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 30px;
    margin-top: 8px;

}
.what__bottom {
    display: flex;
    justify-content: space-between;
    gap: 0 43px;
}
.what__bottom_text {
    color: #81AFDD;
    font-family: Manrope;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 30px;
    max-width: 334px;
}

.what__title {
    color: #FFF;
    font-family: Manrope;
    font-size: 24px;
    font-style: normal;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -0.96px;
    max-width: 151px;
    margin-bottom: 42px;
}
.what__title::before {
    content: "";
    width: 38px;
    height: 3px;
    position: absolute;
    background: linear-gradient(143deg, #AE67FA 0%, #F49867 97.40%), #FFF;

}
@media (max-width: 1220px) {
    .what__bottom {
        flex-wrap: wrap;
        justify-content: space-around;
    }
    .what__bottom-wrapper {
        margin-bottom: 40px;

    }
    .what__top {
        flex-direction: column;
        text-align: center;
        margin: 0 auto;
        align-items: center;
    }
    .what__title {
        text-align: center;
        margin: 0 auto;
    }
}
@media (max-width: 800px) {
    .what__middle {
        align-items: center;
        flex-direction: column;
        text-align: center;
    }
    .what__bottom_text {
        text-align: center;
    }
}
@media (max-width: 500px) {
    .what__middle_text {
        font-size: 28px;
    }
    .what__top_text {
        margin-bottom: 20px;
    }
    .what__middle {
        margin-bottom: 40px;
    }
}
@media (max-width: 630px) {
    .what {
        margin-bottom: 30px;
    
    }
}
