.register {
    display: flex;
    justify-content: space-between;
    margin-top: 154px;
    border-radius: 10.724px;
    border: 1px solid #000;
    background: linear-gradient(90deg, #AE67FA 0%, #F49867 97.40%);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    padding-top:46px ;
    padding-left: 46px;
    padding-right: 37px;
    padding-bottom: 55px;
    margin-bottom: 154px;
}
.register__left a {
    color: #0E0E0E;
    font-family: Manrope;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 30px;
    max-width: 318px;

}
.register__left p {
    color: #000;
    font-family: Manrope;
    font-size: 24px;
    font-style: normal;
    font-weight: 800;
    line-height: 45px;
    max-width: 800px;
}
.register__btn {
    color: #FFF;
    font-family: Manrope;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 30px;
    border-radius: 40px;
    background: #000;
    padding: 13px 42px 16px 45px;
    transition: all 0.5s ease;
    margin-top: 11px;
}
.register__btn:hover {
    opacity: 0.7;
}
@media (max-width:1200px) {
    .register {
        flex-direction: column;
        text-align: center;
        margin: 0 auto;

    }
    .register__left p {
        text-align: center;
        margin: 0 auto;
    }
}
@media (max-width: 630px) {
    .register {
        margin-bottom: 30px;
    }
}
