.features {
    padding-bottom: 155px;
}
.features__wrapper{
    display: flex;
    justify-content: space-between;
}
.features__left_title {
    font-family: Manrope;
    font-size: 34px;
    font-style: normal;
    font-weight: 800;
    line-height: 45px;
    max-width: 426px;
    background: linear-gradient(#AE67FA, #F49867) ;
    background-clip: text;
    color: transparent;
    margin-bottom: 34px;
}
.features__link {
    color: #FF8A71;
    font-family: Manrope;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 30px;
}
.features__right {
    display: flex;
    flex-direction: column;
    padding-bottom: 35px;

}
.features__right-item {
    display: flex;
    margin-bottom: 20px;
}
.features__right-item-title {
    color: #FFF;
    font-family: Manrope;
    font-size: 18px;
    font-style: normal;
    font-weight: 800;
    line-height: 24px;
    letter-spacing: -0.72px;
    max-width: 182px;
    margin-right: 74px;
}
.features__right-item-text {
    color: #81AFDD;
    font-family: Manrope;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    max-width: 334px;
}
.features__bottom {
    display: flex;
    align-items: flex-end;

}
.features__bottom_img {
    max-width: 546px;
    margin-top: 77px;
    margin-right: 19px;
}
.features__bottom_right-link1 {
    color: #71E5FF;
    font-family: Manrope;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 30px;
    margin-bottom: 10px;
}
.features__bottom_right-link2 {
    color: #FF8A71;
    font-family: Manrope;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 30px;
}
.features__bottom_right h3{
    font-family: Manrope;
    font-size: 34px;
    font-style: normal;
    font-weight: 800;
    line-height: 45px;
    background: linear-gradient(#AE67FA, #F49867);
    background-clip: text;
    color: transparent;
    max-width: 426px;
    margin-bottom: 23px;
}
.features__bottom_right p {
    color: #81AFDD;
    font-family: Manrope;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
    max-width: 479px;
    margin-bottom: 31px;
}
.features__bottom_right {
    margin-bottom: 35px;
}
@media (max-width: 1100px) {
    .features__wrapper {
        flex-direction: column;
    }
    .features__left {
        text-align: center;
        margin: 0 auto;
        margin-bottom: 30px;
    }
    .features__right {
        text-align: center;
        margin: 0 auto;
    }
    .features__bottom {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .features__bottom_img {
        margin-bottom: 30px;
        margin-right: 0px;
        margin-left: 65px;
        margin-top: 30px;
    }
}
@media (max-width: 630px) {
    .features__bottom_img {
        max-width: 300px;
    }
    .features__right-item {
        flex-direction: column;
        text-align: center;
        margin: 0 auto 15px auto;
    }
    .features__right-item-title {
        margin-right: 0;
        text-align: center;
        margin: 0 auto;
        margin-bottom: 15px;
    }
    .features__bottom_right {
        margin-right: 0;
        text-align: center;
    }
    .features__bottom_right h3 {
        margin-right: 0;
        text-align: center;
        margin: 0 auto 15px auto;
    }
    .features {
        padding-bottom: 30px;
    }
}
@media (max-width: 500px) {
    .features__left_title {
        font-size: 26px;
    }
    .features__bottom_right h3 {
        font-size: 26px;
    }
    .features__bottom_img {
        max-width: 260px;
    }
}
