.blog {
    margin-bottom: 154px;
}
.blog__item_img {
    max-width: 417px;
   
}
.blog__item1 {
    display: flex;
    flex-direction: column;
}
.blog__item2 {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.blog__content {
    display: grid;
    grid-template-columns: 417px 328px 328px;
    gap: 0 50px;
}
.blog__item_inner_img {
    max-width: 327px;
}
.blog__inner-content {
    background: #042C54;
    padding: 8px 17px 15px 26px;
}
.blog__inner-content span {
    margin-bottom: 8px;
}
.blog__inner-content p {
    margin-bottom: 40px;
    color: #FFF;
    font-family: Manrope;
    font-size: 19.315px;
    font-style: normal;
    font-weight: 800;
    line-height: 30.287px;
}
.blog__inner-content a {
    color: #FFF;
    font-family: Manrope;
    font-size: 11.649px;
    font-style: normal;
    font-weight: 700;
    line-height: 34.947px;
}
.blog__item_inner {
    max-width: 327px;
}



.blog__item_content {
    background: #042C54;
   
    padding: 32px 50px 26px 25px;

}
.blog__date {
    color: #FFF;
    font-family: Manrope;
    font-size: 11.649px;
    font-style: normal;
    font-weight: 700;
    line-height: 34.947px;
}
.blog__item_content p {
    color: #FFF;
    font-family: Manrope;
    font-size: 25.11px;
    font-style: normal;
    font-weight: 800;
    line-height: 30.287px;
    max-width: 343px;
    margin-bottom: 187px;
}
.blog__item_content a {
    color: #FFF;
    font-family: Manrope;
    font-size: 11.649px;
    font-style: normal;
    font-weight: 700;
    line-height: 34.947px;
}
.blog__title {
    font-family: Manrope;
    font-size: 62px;
    font-style: normal;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -2.48px;
    max-width: 701px;
    margin-bottom: 115px;
    background: linear-gradient(#AE67FA, #F49867);
    background-clip: text;
    color: transparent;
}
@media (max-width:1220px) {
    .blog__content {
        grid-template-columns: 328px;
        justify-content: center;
        gap: 0px 0;
    }
    .blog__item1 {
        margin-bottom: 50px;
    }
    .blog__title {
        text-align: center;
        margin: 0 auto 115px auto;
    }
    .blog__item_inner {
        margin-bottom: 50px;
    }
}
@media (max-width:1200px) {
    .blog {
        margin-top: 40px;
    }
    
}
@media (max-width:700px) {
    .blog__title {
        font-size: 34px;
        margin: 0 auto 40px auto;
    }
    
}
@media (max-width:630px) {
    .blog {
        margin-bottom: 30px;
    }
}
@media (max-width:500px) {
    .blog__title {
        font-size: 26px;
       
    }
    
}
