.intro{
    margin-top: 103px;
    display: flex;
    margin-bottom: 119px;
}
.intro__title{
    font-family: Manrope;
    font-size: 62px;
    font-style: normal;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -2.48px;
    max-width: 614px;
    background: linear-gradient(#AE67FA, #F49867);
    background-clip: text;
    color: transparent;
    margin-bottom: 33px;
}
.intro__text {
    color: #81AFDD;
    font-family: Manrope;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    max-width: 554px;
    margin-bottom: 45px;
}
.intro__form {
    display: flex;
    margin-bottom: 38px;

}
.intro__form input {
    padding: 30px 200px 26px 32px;
    border-radius: 5px 0px 0px 5px;
    background: #052D56;
    color: #3D6184;
    font-family: Manrope;
    font-weight: 400;
    font-size: 20px;
}
.intro__form button {
    padding: 20px 26px 26px 34px;
    border-radius: 5px 0px 0px 5px;
    background: #052D56;
    border-radius: 0px 5px 5px 0px;
    background: #FF4820;
    color: #FFF;
    font-family: Manrope;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    transition:  ease 0.5s;
}
.intro__form button:hover {
    opacity: 0.7;
}
.intro__form input::placeholder {
    color: #3D6184;
    font-family: Manrope;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}
.intro__right-part-img {
    max-width: 667.41px;
    position: absolute;
    top: 111px;
}
.intro-people {
    display: flex;
    align-items: center;
}
.intro-people img {
    margin-right: 21px;
}
.intro-people span {
    color: #FFF;
    font-family: Manrope;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 37.611px;
}
.menu-open {
    overflow: hidden;
  }
  
@media (max-width:1500px) {
    .intro {
        flex-direction: column;
        text-align: center;
    }
    .intro__right-part-img {
        position: static;
    }
    .intro__title {
        margin: 0 auto 33px auto;
    }
    .intro__text {
        text-align: center;
        margin: 0 auto 45px auto;

    }
    .intro__form {
        justify-content: center;
        text-align: center;
        margin: 0 auto 45px auto;
    }
    .intro-people {
        justify-content: center;
    }
}
@media (max-width:710px) {
    .intro__right-part-img {
        max-width: 400px;
    }
    .intro__form input {
        padding: 26px 30px 22px 32px;
    }
    .intro__title {
        font-size: 40px;
    }
}
@media (max-width:510px) {
    .intro {
        margin-top: 50px;
    }
    .intro__right-part-img {
        max-width: 400px;
    }
    .intro__form input {
        padding: 26px 15px 22px 15px;
        margin-bottom: 15px;
        border-radius: 5px;
    }
    .intro__form button {
        border-radius: 5px;
    }
    .intro__form {
        margin-bottom: 20px;
    }
    .intro__form  {
        flex-direction: column;
    }
    .intro__title {
        word-spacing: 5px;
        line-height: 40px;
        font-size:30px ;
        margin-bottom: 20px;
    }

}
@media (max-width:540px) {
    .intro-people {
        flex-direction: column;
   
}
}
@media (max-width:444px) {
    .intro__right-part-img {
        max-width: 300px;
   
}
}

@media (max-width: 710px) {
    .intro{
        margin-bottom: 30px;
    }
}